

.background {
  background-image: url("./background.jpg");
  background-size: cover;
  background-position: center center;
  position:fixed;
    top: 0px; /* Header Height */
    bottom: 0px; /* Footer Height */
    left: 0px;
    right: 0px;
    
}

.backgroundOverlay {
  background-color: rgba(242, 242, 242, 0.5);
  position: absolute;
  top: 0px; /* Header Height */
  bottom: 0px; /* Footer Height */
  left: 0px;
  right: 0px;

}

