	position: absolute;
	top: 0px;
	right: 0px;
}*/

.allNav {
	margin-left: 10%;
	position: relative;
	
}

.logo {
	display: flex;
	margin-top: 20px;
	margin-right: 60px;
	width: 60%;
	max-width: 800px;
	min-width: 500px;
	
}

.nav {
	width: 60%;
	max-width: 900px;
	min-width: 500px;
}

/*check app css for sizing*/
.navButton {

	background-color: rgba(242, 242, 242, 0.6);
  z-index: +1;
}

.contentContainer {
  display: flex;
  justify-content: center;
  position: absolute;
  width: 100%;
  bottom: 10px;
  top: 60px;
/*  left: 10%;*/
  overflow: scroll;
  overflow-x: hidden;

}
::-webkit-scrollbar {
    width: 0px;  /* Remove scrollbar space */
    background: transparent;  /* Optional: just make scrollbar invisible */
}


footer {
  position: fixed;
  bottom: 0;
  width: 100%;
}

.social {
	margin-bottom: 10px;
	width: 50%;
	max-width: 550px;
	margin-left: 22%;
}

@media only screen and (max-width: 600px) {
    .allNav {
        margin-left: 0px;
        
    }
    .contentContainer {
    	left: 0;
    }
}